import { useEffect, useState } from "react";
import styles from "./SettingPage.module.scss";
import { AlertModal } from "components/modals/AlertModal";
import { AuthInput } from "components/AuthInput";
import { getProfile, updateProfile } from "api/userinfo";
// import { useAuth } from "context/authContext";
// import { useNavigate } from "react-router";
import { useCurrentUser } from "context/userInfoContext";
import clsx from "clsx";

export const SettingPage = () => {
  const [accountPassed, setAccountPassed] = useState(true);
  const [emailPassed, setEmailPassed] = useState(true);
  const [pwdPassed, setPwdPassed] = useState(true);
   const [namePassed, setNamePassed] = useState(true);
   const [showAlert, setShowAlert] = useState(false);
   const [alertWord, setAlertWord] = useState("");
   const [alertIcon, setAlertIcon] = useState(false)

  const [user, setUser] = useState({});
  const [nameLength, setNameLength] = useState("");
  // const { isAuthenticated } = useAuth();
  const { profile, setProfile } = useCurrentUser();
  // const navigate = useNavigate();
  const id = localStorage.getItem("id");
  // useEffect(() => {
  //   if (!isAuthenticated) {
  //     navigate("/login");
  //     return;
  //   } else {
  //     navigate(`/setting`);
  //     return;
  //   }
  // }, [isAuthenticated, navigate]);

  useEffect(() => {
    const showPage = async () => {
      setProfile(await getProfile(id));
      // setNameLength(profile.name.length);
    };
    showPage();
    console.log("3600 test from setting page");
  }, [id, setProfile]);


  useEffect(() => {
    if (showAlert) {
      const timeout = setTimeout(() => {
        setShowAlert(false);
        setAlertWord("");
      }, 3000);
      console.log("register useEffect");
      return () => clearTimeout(timeout); // cleanup function
    }
  }, [showAlert]);

  const handleSave = async () => {
    if (user.account === "") {
      // alert("");
      setShowAlert(true);
      setAlertWord("");
      setAlertIcon(false)
      setAccountPassed(false);
      return;
    }
    if (user.name === "") {
      // alert("");
       setShowAlert(true);
       setAlertWord("");
       setAlertIcon(false);
       setNamePassed(false);
      return;
    }
    if (user.email === "") {
      // alert("Email");
      setShowAlert(true);
      setAlertWord("Email");
      setAlertIcon(false);
      setEmailPassed(false);
      return;
    }
    if (user.password !== user.checkPassword) {
      // alert("");
      setShowAlert(true);
      setAlertWord("");
      setAlertIcon(false);
      setPwdPassed(false);
      return;
    }
    if (nameLength > 50) {
      // alert("50");
      setShowAlert(true);
      setAlertWord("50");
      setAlertIcon(false);
      setNamePassed(false)
      return;
    }
    const { success, data } = await updateProfile(user);
    console.log(success);

    if (success) {
      // alert("");
       setShowAlert(true);
       setAlertWord("");
       setAlertIcon(true);
      console.log(data);
      setTimeout(() => {
        const reload = () => window.location.reload(true);
        reload();
      },1000)
      
    } else {
      if (data.response.data === "This account has been used!") {
        // alert("");
        setShowAlert(true);
        setAlertWord("");
        setAlertIcon(false);
        setAccountPassed(false);
        return;
      } else if (data.response.data === "This email has been used!") {
        // alert("Email");
        setShowAlert(true);
        setAlertWord("Email");
        setAlertIcon(false);
        setEmailPassed(false);
        return;
      } else return;
    }
  };

  return (
    <>
      <div className={styles.mainbarContainer}>
        <header className={styles.settingPageHeader}>
          <h1 className={styles.settingPageTitle}></h1>
        </header>

        <div className={styles.inputContainer}>
          <AuthInput
            type="text"
            label=""
            value={profile.account}
            accountPassed={accountPassed}
            onChange={(e) => {
              setAccountPassed(true);
              setUser({
                ...user,
                account: e.target.value,
              });
            }}
          />
          <span
            className={clsx("", {
              [styles.limit]: !accountPassed,
              [styles.noLimit]: accountPassed,
            })}
          >
            <p>!</p>
          </span>
        </div>
        <div className={styles.inputContainer}>
          <AuthInput
            type="text"
            label=""
            value={profile.name}
            namePassed={namePassed}
            onChange={(e) => {
              setNamePassed(true);
              setNameLength(e.target.value.length);
              setUser({
                ...user,
                name: e.target.value,
              });
            }}
          />
          <span
            className={clsx("", {
              [styles.limit]: !namePassed,
              [styles.noLimit]: namePassed,
            })}
          >
            <p>!</p>
            <p>{nameLength}/50</p>
          </span>
          <span
            className={clsx("", {
              [styles.limit]: nameLength > 50,
              [styles.noLimit]: nameLength <= 50,
            })}
          >
            <p>!</p>
            <p>{nameLength}/50</p>
          </span>
        </div>
        <div className={styles.inputContainer}>
          <AuthInput
            type="text"
            label="Email"
            value={profile.email}
            emailPassed={emailPassed}
            onChange={(e) => {
              setEmailPassed(true);
              setUser({
                ...user,
                email: e.target.value,
              });
            }}
          />
          <span
            className={clsx("", {
              [styles.limit]: !emailPassed,
              [styles.noLimit]: emailPassed,
            })}
          >
            <p>Email!</p>
          </span>
        </div>
        <div className={styles.inputContainer}>
          <AuthInput
            type="password"
            label=""
            value=""
            pwdPassed={pwdPassed}
            placeholder=""
            onChange={(e) => {
              setPwdPassed(true);
              setUser({
                ...user,
                password: e.target.value,
              });
            }}
          />
          <span
            className={clsx("", {
              [styles.limit]: !pwdPassed,
              [styles.noLimit]: pwdPassed,
            })}
          >
            <p>!</p>
          </span>
        </div>
        <div className={styles.inputContainer}>
          <AuthInput
            type="password"
            label=""
            value=""
            pwdPassed={pwdPassed}
            placeholder=""
            onChange={(e) => {
              setPwdPassed(true);
              setUser({
                ...user,
                checkPassword: e.target.value,
              });
            }}
          />
          <span
            className={clsx("", {
              [styles.limit]: !pwdPassed,
              [styles.noLimit]: pwdPassed,
            })}
          >
            <p>!</p>
          </span>
        </div>
        <button className={styles.btn} onClick={handleSave}>
          
        </button>
        {showAlert && <AlertModal value={alertWord} alertIcon={alertIcon} />}
      </div>
    </>
  );
};
